import React, { Component } from 'react'
import { Panel, Avatar, Div, Progress, Spinner, FixedLayout, InfoRow } from '@vkontakte/vkui';

function getStatus(n) {
    let name = ''
     if(n == 0 || n == 1) {
         return '';
     }else if(n >= 2 && n < 5) {
         return ''
     } else if(n >=5 && n < 10) {
         return ' '
     } else if(n >=10 && n < 15) {
         return ''
     } else {
         return ' '
     }

}

const Load =  ({ progress, is_first,state, user}) => (
    
     <Div>
         <Div align='center'>
             <Avatar size={96} src='https://sun9-9.userapi.com/cBhXydLqUUQkHDWZkxYtVMivVmuxM1IRo5JWkQ/yvpKLp5qKus.jpg'/>
         </Div>

         {is_first && <div style={{ textAlign: 'center', marginTop:12}}><br /> </div>}
         
                <FixedLayout vertical='bottom'>
                {!is_first && <div style={{ marginBottom: 12 }} align='center'><span>{state.user.first_name + " " + state.user.last_name}</span><br />{getStatus(state.my_groups.length + state.countDB)}</div>}
                    <Spinner style={{ marginBottom: 12 }}/>
                    <Progress value={progress} />
                </FixedLayout>
             
         
     </Div>

)

export default Load;